header {
  display: flex;
  background-color: black;
  box-sizing: border-box;
  font-size: 24px;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  color: red;
  font-weight: 500;
}

.nav_bar__menu {
  display: flex;
  justify-content: center;
  text-align: center;
  list-style: none;
}

.nav_span {
  margin-left: 60px;
}

.nav_bar__menu li {
  margin-right: 60px;
  display: inline-block;
  position: relative;
}

.nav_bar__menu a,
.nav_span {
  text-decoration: none;
  color: red;
}

.nav_bar__menu li::after {
  position: absolute;
  content: "";
  background-color: red;
  width: 0;
  height: 1.5px;
  left: 50%;
  display: block;
  transform: translate(-50%);
  transition: all 0.2s;
}

.nav_bar__menu li:hover::after {
  width: 100%;
}
