.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
  font-size: 150px;
}

.banner {
  position: relative;
  height: 60vh;
  width: 100%;
  overflow: hidden;
  margin: 0 auto;
}

.banner::before {
  position: absolute;
  width: 100%;
  height: 70vh;
  content: "";
  background: linear-gradient(to right, black, transparent);
  opacity: 0.9;
}

.banner_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.banner_infor {
  position: absolute;
  width: 600px;
  margin-left: 80px;
  padding-left: 20px;
  top: 30%;
  color: white;
  z-index: 1;
}

@media screen and (max-width: 1090px) {
  .page {
    grid-template-columns: 1fr;
    width: 100%;
  }
}
