.first_container {
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: black;
  font-size: 70px;
  text-decoration: none;
  color: inherit;
}

.movie_box,
.TV_box {
  padding: 100px;
}

.first_a {
  text-decoration: none;
  color: red;
}

.first_span:hover {
  transform: scale(1.2);
  transition: transform 0.2s;
}

.first_span:not(hover) {
  transition: 0.1s ease-out;
}

@media screen and (max-width: 1090px) {
  .first_container {
    font-size: 45px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
  }
}
